
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { getTradelist } from 'api'
export default {
data () {
return {
cc: this.$route.query.cc,
acc: this.$route.query.acc,
list: [],
page: 1,
noData: true,
loading: false
}
},
created () {
this.getTradelist()
},
methods: {
getTradelist () {
let args = {
method: 'history',
cc: this.cc,
acc: this.acc,
page: this.page,
number: 20
}
this.loading = true
getTradelist(args).then(res => {
let list = res.data
if (this.page <= 1) {
this.list = list
} else {
this.list.push(...list)
}
this.noData = (list.length < 10)
this.loading = false
})
.finally(() => {
this.loading = false
})
},
loadMore () {
if (this.acc) {
this.noData = true
this.page += 1
this.getTradelist()
}
}
},
components: {
XHeader,
LoadMore
}
}



